<template>
  <div id="content">
    <PathControl :path="path"/>

    <el-dialog title="" :visible.sync="showImg" width="700px">
      <img :src="curImgUrl" class="mid" alt="" @click="zoom_img">
    </el-dialog>

    <el-dialog title="" :visible.sync="showImgBig" :fullscreen=true :show-close=false>
      <img :src="curImgUrl" class="full" alt="">
    </el-dialog>

    <ImgDisplay  v-if="isImgFolder" :imgList="imgList" ref="ImgDisplay"/>
    <FolderDisplay v-else :dirList="dirList"/>

  </div>
</template>

<script>
import {Bus} from '../event-bus'
import PathControl from './PathControl.vue'
import ImgDisplay from './ImgDisplay.vue'
import FolderDisplay from './FolderDisplay.vue'

export default {
  name: 'Content',
  components: {
    PathControl,
    ImgDisplay,
    FolderDisplay
  },
  data () {
    return {
      path: '',
      isImgFolder: true,
      dirList: [],
      imgList: [],
      curImgUrl: '',
      showImg: false,
      showImgBig: false,
      imgIdx: 0
    }
  },
  created () {
    Bus.$on('Dataset', this.load_dataset)
    Bus.$on('Project', this.load_project)
    Bus.$on('Command', this.load_command)
    this.load_path()
    document.onkeydown = (event) => {
      if (!this.showImg) return
      if (event.keyCode === 39) { // <-
        let idx = this.imgIdx + 1
        if (idx >= this.imgList.length) return
        this.click_item(this.imgList[idx], idx)
      }
      if (event.keyCode === 37) { // ->
        let idx = this.imgIdx - 1
        if (idx < 0) return
        this.click_item(this.imgList[idx], idx)
      }
    }
  },
  methods: {
    load_dataset () {
      if (this.$storage.datasetPath) {
        this.path = this.$storage.datasetPath
      } else {
        this.path = 'dataset'
      }
      this.load_path()
    },
    load_project () {
      if (this.$storage.projectPath) {
        this.path = this.$storage.projectPath
      } else {
        this.path = 'project'
      }
      this.load_path()
    },
    load_command () {
      this.path = 'command'
      alert('unavailable now!')
      this.imgList = []
      this.dirList = []
    },
    click_item (item, idx) {
      if (item.type === 'image') {
        this.curImgUrl = item.url
        this.showImg = true
        this.imgIdx = idx
        return
      } else if (item.type === 'file') {
        return
      }
      this.path = item.path
      this.load_path()
    },
    parent_path () {
      this.path = this.path.split('/')
      if (this.path.length > 1) {
        this.path.pop()
      }
      this.path = this.path.join('/')
      this.load_path()
    },
    load_path () {
      if (this.path.indexOf('dataset') !== -1) {
        this.$storage.datasetPath = this.path
      } else if (this.path.indexOf('project') !== -1) {
        this.$storage.projectPath = this.path
      }
      this.$axios.get('imagesite/api/path?dir=' + this.path)
        .then(response => {
          let data = response.data
          this.isImgFolder = data.is_img_folder
          if (this.isImgFolder) {
            this.imgList = data.items
          } else {
            this.dirList = data.items
          }
        })
        .catch(function (error) {
          console.log(error)
        })
    },
    zoom_img () {
      this.showImgBig = true
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #content {
    display: inline-block;
    width: 1000px;
    margin: 20px;
    padding: 30px;
    background-color: RGBA(102, 153, 255, 0.1);
  }
  er_col {
    margin: 10px;
  }
  img.mid {
    max-width:600px;
  }
  /*img.full {*/
    /*width: 100%;*/
  /*}*/
</style>



