<template>
  <div>
    <el-row :gutter="20">
      <el-col :span="6" v-bind:key="index" v-for="(item, index) in imgCurList"
              style="margin-bottom: 20px">
        <el-card :body-style="{ padding: '10px', cursor: 'pointer', height: '100px', lineHeight: '150px'}"
                 @click.native="click_item(item, index)">
          <img :src="item.smallurl" alt="">
        </el-card>
      </el-col>
    </el-row>
    <el-pagination
      background
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="imgPageNo"
      :page-sizes="[20, 40, 60, 100]"
      :page-size="imgPageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="imgCount">
    </el-pagination>
  </div>
</template>

<script>

export default {
  name: 'ImgDisplay',
  data () {
    return {
      imgCurList: [],
      imgPageNo: 1,
      imgPageSize: 20,
      imgCount: 0
    }
  },
  props: ['imgList'],
  mounted () {
    this.handleCurrentChange(1)
    this.imgCount = this.imgList.length
  },
  watch: {
    imgList: () => {
      alert(1)
      this.handleCurrentChange(1)
      this.imgCount = this.imgList.length
    }
  },
  methods: {
    handleCurrentChange (pageNo) {
      this.imgPageNo = pageNo
      let start = (this.imgPageNo - 1) * this.imgPageSize
      this.imgCurList = this.imgList.slice(start, start + this.imgPageSize)
    },
    handleSizeChange (pageSzie) {
      this.imgPageSize = pageSzie
      this.handleCurrentChange(1)
    },
    click_item (item, index) {
      let imgIdx = this.imgPageNo * this.imgPageSize - this.imgPageSize + index
      this.$parent.click_item(item, imgIdx)
    }
  }
}
</script>

<style scoped>
</style>



