<template>
  <div id="pathDiv">
    <div>
      <el-input v-model="path" readonly="true"></el-input>
    </div>
    <div>
      <el-button type="primary" plain @click="parent_path">Parent Folder</el-button>
    </div>
  </div>
</template>

<script>

export default {
  name: 'PathControl',
  data () {
    return {}
  },
  props: ['path'],
  mounted () {
    history.pushState(null, null, document.URL)
    this.$window.addEventListener('popstate', () => {
      this.parent_path()
      history.pushState(null, null, document.URL)
    })
  },
  methods: {
    parent_path () {
      this.$parent.parent_path()
    }
  }
}
</script>

<style scoped>
  #pathDiv {
    height: 80px;
  }
  #pathDiv div {
    display: inline-block;
  }
  #pathDiv div:nth-child(1) {
    float: left;
    width: 800px;
  }
  #pathDiv div:nth-child(2) {
    float: right;
    width: 120px;
  }
</style>



