<template>
  <div id="navbar">
    <div>
      <el-radio-group v-model="mode" @change="changeMode">
        <el-radio-button label="Dataset"></el-radio-button>
        <el-radio-button label="Project"></el-radio-button>
        <el-radio-button label="Command"></el-radio-button>
        </el-radio-group>
      </div>
  </div>
</template>

<script>
import {Bus} from '../event-bus'

export default {
  name: 'NavBar',
  data () {
    return {
      mode: 'Dataset'
    }
  },
  mounted () {
    this.changeMode('Dataset')
  },
  methods: {
    changeMode () {
      let mode = this.mode
      Bus.$emit(mode)
      console.log(mode)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #navbar {
    min-width: 704px;
  }
</style>



