<template>
  <el-row :gutter="20">
    <el-col :span="6" v-bind:key="index" v-for="(item, index) in dirList"
            style="margin-bottom: 20px">
      <el-card
        :body-style="{ padding: '20px', cursor: 'pointer'}"
        @click.native="click_item(item)">
        <el-tooltip v-if="item.dir_name.length > 20"
                    class="item" effect="dark" :content="item.dir_name" placement="top-end">
          <div>
            <span  v-if="item.type === 'file'" class="dib"><i class="el-icon-document"></i></span>
            <span  v-else class="dib"><i class="el-icon-goods"></i></span>
            <span class="file_name dib">{{item.dir_name}}</span>
          </div>
        </el-tooltip>
        <div v-else>
          <span  v-if="item.type === 'file'" class="dib"><i class="el-icon-document"></i></span>
          <span  v-else class="dib"><i class="el-icon-goods"></i></span>
          <span class="file_name dib">{{item.dir_name}}</span>
        </div>
      </el-card>
    </el-col>
  </el-row>
</template>

<script>

export default {
  name: 'FolderDisplay',
  data () {
    return {}
  },
  props: ['dirList'],
  mounted () {
  },
  methods: {
    click_item (item) {
      this.$parent.click_item(item)
    }
  }
}
</script>

<style scoped>
  .dib {
    display: inline-block;
    vertical-align: top;
  }
  .file_name {
    text-overflow: ellipsis;
    overflow: hidden;
    max-width: 170px;
    white-space: nowrap ;
  }
</style>



